.loader {
  text-align: left;
  color: #000;
  margin: 0;
  padding: 0 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 56px;
  display: inline-block;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  width: 5px;
  height: 5px;
  color: #00f;
  background: currentColor;
  animation: load 2s ease-in-out infinite alternate;
  display: block;
  position: absolute;
  top: 0;
}

.loader:after {
  top: auto;
  bottom: 0;
}

@keyframes load {
  0% {
    height: 43px;
    width: 15px;
    left: 0;
    transform: translateX(0);
  }

  50% {
    height: 10px;
    width: 40px;
  }

  100% {
    height: 43px;
    width: 15px;
    left: 100%;
    transform: translateX(-100%);
  }
}

.cat-info {
  gap: 20px;
  display: flex;
}

.info {
  max-width: 500px;
}

.text-explanation {
  max-width: 500px;
  font-weight: 600;
}

.hidden {
  display: none;
}

.visible {
  display: block;
}

.breed-select {
  text-align: left;
  margin: 14px 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 56px;
  position: relative;
}

